<template>
  <div>
    <vue-confirm-dialog></vue-confirm-dialog>

    <div class="row">
      <div class="col-md-12">
        <v-card>
          <v-card-title>
            Fill in Form Data
          </v-card-title>
          <v-card-text>
            <validation-observer ref="observer" v-slot="{ handleSubmit }">
              <b-form @submit.stop.prevent="handleSubmit(onSubmit)">
                <validation-provider
                  name="plan"
                  rules="required"
                  v-slot="validationContext"
                >
                  <b-form-group label="Plans">
                    <b-form-select
                      v-model="form.plan"
                      :options="plans"
                      class="mb-2 mr-sm-2 mb-sm-0"
                      :state="getValidationState(validationContext)"
                      aria-describedby="f_type"
                    >
                      <!-- This slot appears above the options from 'options' prop -->
                      <template v-slot:first>
                        <b-form-select-option :value="null"
                          >-- Please select an option --</b-form-select-option
                        >
                      </template>
                    </b-form-select>
                    <b-form-invalid-feedback id="f_type">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>

                <validation-provider
                  name="type"
                  rules="required"
                  v-slot="validationContext"
                >
                  <b-form-group label="Types">
                    <b-form-select
                      v-model="form.type"
                      :options="types"
                      class="mb-2 mr-sm-2 mb-sm-0"
                      :state="getValidationState(validationContext)"
                      aria-describedby="type"
                    >
                      <!-- This slot appears above the options from 'options' prop -->
                      <template v-slot:first>
                        <b-form-select-option :value="null"
                          >-- Please select an option --</b-form-select-option
                        >
                      </template>
                    </b-form-select>
                    <b-form-invalid-feedback id="type">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>
                <validation-provider
                  name="Title"
                  rules="required|min:5"
                  v-slot="validationContext"
                >
                  <b-form-group label="Title">
                    <b-form-input
                      v-model="form.title"
                      :state="getValidationState(validationContext)"
                      aria-describedby="input-1-live-feedback"
                    ></b-form-input>

                    <b-form-invalid-feedback id="input-1-live-feedback">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>
                <validation-provider
                  name="Message"
                  rules="required"
                  v-slot="validationContext"
                >
                  <b-form-group label=" Message">
                    <b-form-textarea
                      v-model="form.message"
                      placeholder="Enter something..."
                      rows="3"
                      max-rows="6"
                      :state="getValidationState(validationContext)"
                      aria-describedby="input-3-live-feedback"
                    ></b-form-textarea>

                    <b-form-invalid-feedback id="input-3-live-feedback">
                      {{ validationContext.errors[0] }}
                    </b-form-invalid-feedback>
                  </b-form-group>
                </validation-provider>

                <b-button type="submit" variant="success">Save</b-button>
                <b-button class="ml-2" variant="warning" @click="resetForm()"
                  >Reset</b-button
                >
              </b-form>
            </validation-observer>
          </v-card-text>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

const VueScrollTo = VueScrollTo;

export default {
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Notify Users", route: "notify" },
    ]);
  },
  data() {
    return {
      records: [],
      plans: [],
      types: [
        { value: "1", text: "العملاء" },
        { value: "2", text: "الوكلاء " },
      ],
      edit: false,
      record_id: "",
      form: {
        plan: null,
        type: null,
        title: null,
        message: null,
      },
    };
  },

  created() {
    this.fetchText();
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },

    resetForm() {
      this.form = {
        plan: null,
        type: null,
        title: null,
        message: null,
      };

      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    fetchText() {
      this.$ApiService
        .get("/Plans/Texts")
        .then((response) => {
          //this.records = response.data.data.texts;
          this.plans = response.data.data;
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
    clearFilter() {
      this.records = [{}];
    },

    onSubmit() {
      this.$ApiService
        .post("/Opperations/Filter", this.form)
        .then((response) => {
          if (response.data.error == 1) {
            Vue.$toast.success(`${response.data.result}`);
          } else {
            console.log(response.data);
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => {
          Vue.$toast.error("some error happened, please try again later");
          console.log(err);
        });
    },
  }, // end of methods
}; //end of class
</script>
